// Tab mixins
// --------------------------------------------------


// Tab variant
// -------------------------

.tab-variant(@name; @color) {
	.nav-tabs.@{name} {
		> li {
			> a {
				&:after { 
					transform: scale(0); 
					background:@color; 
					}
			}
			&.active > a {
				&, &:hover, &:focus {
					&::after { transform: scale(1); background:@color; }
					border-color: @color;
					color: @color;
				}
			}
		}
	}
}
