@color-orange: #ff9900;
@color-green: #48b04b;
@color-blue: #00ccff;
@color-purple: #6e4db0;
@color-gray: #5f7d8c;


.text-emphasis-variant(@color) {
  color: @color;
  a&:hover {
    color: darken(@color, 10%);
  }
}


.label-variant(@color) {
  color: darken(@color, 10%);
	background: lighten(@color, 45%);
  // a&:hover {
  //   color: darken(@color, 10%);
  // }
}



//  -> 
.text-zak_formir        { .text-emphasis-variant(@color-orange); }
.text-zak_inwork        { .text-emphasis-variant(@color-green); }
.text-zak_agree         { .text-emphasis-variant(@color-blue); }
.text-zak_lp            { .text-emphasis-variant(@color-purple); }
.text-zak_arch          { .text-emphasis-variant(@color-gray); }

.label-zak_formir        { .label-variant(@color-orange); }
.label-zak_inwork        { .label-variant(@color-green); }
.label-zak_agree         { .label-variant(@color-blue); }
.label-zak_lp            { .label-variant(@color-purple); }
.label-zak_arch          { .label-variant(@color-gray); }

//  -> -
.progress-bar-zak_formir { .progress-bar-variant(@color-orange);  width: 5%;    }
.progress-bar-zak_inwork { .progress-bar-variant(@color-green);   width: 50%;   }
.progress-bar-zak_agree  { .progress-bar-variant(@color-blue);    width: 75%;   }
.progress-bar-zak_lp     { .progress-bar-variant(@color-purple);  width: 85%;   }
.progress-bar-zak_arch   { .progress-bar-variant(@color-gray);    width: 100%;  }

//  -> 
.card-variant(style-zak_formir,  @color-orange);
.card-variant(style-zak_inwork,  @color-green);
.card-variant(style-zak_zgree,   @color-blue);
.card-variant(style-zak_lp,      @color-purple);
.card-variant(style-zak_arch,    @color-gray);


